import {
  getSendMsg
} from '../api'
export default {
  async getSendMsg({commit},) {
    // debugger
    let apiPrefix = 'http://192.168.6.66:8001/api/SMS/SendVerifyCode'
    let data
    const result = await getSendMsg(apiPrefix, data, "post")
    commit('GET_SENDMSG', {result})
  },
}




